import Vue from 'vue'
import Router from 'vue-router'
import page1 from '@/views/page1'
import page2 from '@/views/page2'
import newsindex from '@/views/newsindex'
import mianze from '@/views/mianze'
import sexyimg from '@/views/sexyimg'


Vue.use(Router)

export default new Router({
  mode: 'history', // 去掉url中的#
  routes: [
    {
      path: '/',
      name: 'page1',
      component: newsindex
    },
    {
      path: '/page2',
      name: 'page2',
      component: page2
    },
    {
      path: '/newsindex',
      name: 'newsindex',
      component: newsindex
    },
    {
      path: '/mianze',
      name: 'mianze',
      component: mianze
    },
    {
      path:'/sexyimg',
      name:'sexyimg',
      component:sexyimg
    }
  ]
})




