<template>
    <div class="header">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index="1" href=""><a href="/"><img class="logo-img" src="/static/黑球兽.png">首页</a></el-menu-item>
            <el-submenu index="2">
                <template slot="title">友情链接</template>
                <el-menu-item index="2-1">虚位以待</el-menu-item>
                <el-menu-item index="2-2">虚位以待</el-menu-item>
            </el-submenu>
        </el-menu>
        <div class="line"></div>
    </div>

</template>



<script>
export default {
    name: 'app-header',
    data() {
        return {
            activeIndex: '1',
            activeIndex2: '1'
        };
    },
    methods: {
        handleSelect(key, keyPath) {
            console.log(key, keyPath);
        }
    }
}
</script>


<style scoped>
.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: opacity .3s;
    z-index: 1100;
    box-shadow: 0 2px 4px 0 rgb(85 85 85 / 25%);
    -webkit-font-smoothing: antialiased;
    background: #F3F3F3;
}
.logo-img {
    height: 30px;
    vertical-align: middle;
    padding-bottom: 6px;
}
</style>


