<template >
    <div>
        <div class="yiyan">
            <h3 style="display:inline-block;">{{ yiyan }}</h3><span
                style="font-size: 1px;margin-left: 20px;color: #606266;"> -- {{ yiyanfrom }}</span>
        </div>
        <div class="news-wrapper">
            <div class="weibo">
                <div class="title"><img
                        src="https://img.t.sinajs.cn/t5/style/images/register/logo_big.png?id=1395992824012"></div>
                <template>
                    <el-table :data="weiboresouList" style="width: 100%;" empty-text="正在努力加载中">
                        <el-table-column type="index" :index="indexMethod" />
                        <el-table-column prop="name">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" :title="scope.row.name">{{ scope.row.name }}</a>
                            </template>
                        </el-table-column>
                        <el-table-column prop="hot" align="right">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" >{{ formatNumber(scope.row.hot)
                                }}</a>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>


            <div class="douyin">
                <div class="title"><img
                        src="https://lf3-static.bytednsdoc.com/obj/eden-cn/ulkphgeh7nuvspognuhbo/douyin_web/img/images/logo-horizontal.svg">
                </div>
                <template>
                    <el-table :data="douyinresouList" style="width: 100%" empty-text="正在努力加载中">
                        <el-table-column type="index" :index="indexMethod" />
                        <el-table-column prop="name">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" :title="scope.row.name">{{ scope.row.name }}</a>
                            </template>
                        </el-table-column>
                        <el-table-column prop="hot" align="right">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" >{{ formatNumber(scope.row.hot)
                                }}</a>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>
            </div>

            <div class="toutiao">
                <div class="title"><img src="https://lf3-cdn2-tos.bytescm.com/toutiao/toutiao_web_pc/svgs/logo_red.80c49156.svg">
                </div>
                <template>
                    <el-table :data="toutiaoresouList" style="width: 100%" empty-text="正在努力加载中">
                        <el-table-column type="index" :index="indexMethod" />
                        <el-table-column prop="name">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" :title="scope.row.name">{{ scope.row.name }}</a>
                            </template>
                        </el-table-column>
                        <el-table-column prop="hot" align="right">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" >{{ formatNumber(scope.row.hot)
                                }}</a>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>

                <div class="title"><img src="https://www.baidu.com/img/flexible/logo/pc/result.png" style="    padding: 22px;">
                </div>
                <template>
                    <el-table :data="baiduresouList" style="width: 100%" empty-text="正在努力加载中">
                        <el-table-column type="index" :index="indexMethod" />
                        <el-table-column prop="name">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank" :title="scope.row.name">{{
                                        scope.row.name
                                }}</a>
                            </template>
                        </el-table-column>
                        <el-table-column prop="hot" align="right">
                            <template slot-scope="scope">
                                <a :href="scope.row.url" class="nt" target="_blank">{{ formatNumber(scope.row.hot)
                                }}</a>
                            </template>
                        </el-table-column>
                    </el-table>
                </template>

            </div>



        </div>


    </div>
</template>


<script>
import axios from 'axios'
export default {

    data() {
        return {
            weiboresouList: [],
            toutiaoresouList: [],
            douyinresouList: [],
            baiduresouList: [],
            yiyan: "",
            yiyanfrom: "",
        }
    },
    created() {
        const url = `https://tenapi.cn/v2/`
        axios.post(url+'weibohot').then((res) => {
            this.weiboresouList = res.data.data;
        })
        axios.post(url+'toutiaohot').then((res) => {
            this.toutiaoresouList = res.data.data;
        })

        axios.post(url+'douyinhot').then((res) => {
            this.douyinresouList = res.data.data;
        })

        axios.post(url+'baiduhot').then((res) => {
            this.baiduresouList = res.data.data;
        })

        axios.post('https://v1.hitokoto.cn/').then((res) => {
            this.yiyan = res.data.hitokoto;
            this.yiyanfrom = res.data.from;
        })
    },
    methods: {
        indexMethod(index) {
            return (index + 1) + '.';
        },
        formatNumber(num) {
            num = Number(num);
            if (isNaN(num)) {
                return '';
            }
            if (num == 0) {
                return num + '';
            } else
                if (num > 1 && num < 10000) {
                    return num + '';
                } else {
                    return (num / 10000).toFixed(1) + '万';
                }
        }
    }
}

</script>

<style scoped>
.news-wrapper {
    font-family: Roboto, Arial, sans-serif;
    background: #F7F8FA;
    width: 100%;
    /* height: 200rem; */
    /* position: fixed; */
    display: flex;
}

.weibo,
.toutiao,
.douyin {
    width: 35%;
    background-color: #fff;
    margin: 1.7rem 2rem;
    padding: 20px;
    border-radius: 12px;
    height: 100%;
    box-shadow: 0 2px 7px 0 rgb(5 34 97 / 10%);
}

.toutiao img {
    width: 100px;
}

.douyin img {
    width: 200px;
}

.title {
    color: #000;
    font-size: 24px;
    line-height: 30px;
    max-height: 3.4em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    float: left;
    margin-top: 1.7rem 4rem !important;
}

.nt:hover {
    color: blue;
    text-align: 'center';
}

a {
    text-decoration: none;
    color: #606266;
}

.yiyan {
    margin-top: 5rem;
    color: #606266;
    box-shadow: 0 2px 7px 0 rgb(5 34 97 / 10%);
}
</style>


