<template>
    <div class="footer">
        <meting-js server="netease" type="playlist" id="3778678" autoHide="true" fixed="true" listFolded="true">
        </meting-js>
        <div class="copyright">
            <a @click="tomianze">免责声明</a>
            <span class="separator hideIn768Only"> | </span>
            <span class="text"> © 2022 </span>
            <br>
        </div>
    </div>
</template>
 
<script>
export default {
    name: 'app-footer',
    data() {
        return {
        }
    },
    methods: {
        tomianze() {
            this.$router.push('/mianze')
        }
    },
    created() {
        var _hmt = _hmt || [];
        (function () {
            var hm = document.createElement("script");
            hm.src = "https://hm.baidu.com/hm.js?b99d11d3dbc322d9c29728258d4dde96";
            var s = document.getElementsByTagName("script")[0];
            s.parentNode.insertBefore(hm, s);
        })();
    },
    mounted() {

        let apcss = document.createElement('link');
        apcss.rel = 'stylesheet';
        apcss.href = 'https://cdn.jsdelivr.net/npm/aplayer/dist/APlayer.min.css';
        document.head.appendChild(apcss);

        let apscript = document.createElement('script');
        apscript.type = 'text/javascript';
        apscript.src = 'https://cdn.jsdelivr.net/npm/aplayer/dist/APlayer.min.js';
        document.head.appendChild(apscript);

        let mescript = document.createElement('script');
        mescript.type = 'text/javascript';
        mescript.src = 'https://cdn.jsdelivr.net/npm/meting@2.0.1/dist/Meting.min.js';
        document.head.appendChild(mescript);



    }
}
</script>
<style scoped>
.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    transition: opacity .3s;
    z-index: 1100;
}
</style>


