<template>
  <div>
    <span>{{ msg }}</span>
    <button @click="tiaozhuan">跳转</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: 'page2'
    }
  },
  methods: {
    tiaozhuan() {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
</style>



